*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.hero{
  background: blue;
display: flex;
justify-content: center;
}

.hero main{
  max-width: 460px;
  background: #d4d1d1;
  width: 100%;
  padding: 60px 0 100px 0;
}

.Logo2{
  width: 100px;
}

nav{
  max-width: 460px;
  width: 100%;
  padding: 10px;
  background-color: rgb(51, 143, 39);
  position: fixed;
  top: 0;
  z-index: 1000;
  display: flex;
  justify-content: center;
  border-radius: 0 0 15px 15px;
}

footer{
  max-width: 460px;
  width: 100%;
  padding: 5px;
  background-color: rgb(51, 143, 39);
  color: rgb(17, 14, 173);
  position: fixed;
  bottom: 0;
  z-index: 1000;
  display: flex;
  justify-content: space-around;
  border-radius: 15px 15px 0 0;
}

.footer-menu{
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.nodec{
  text-decoration: none;
  color: rgb(17, 14, 173);
}

.nodec:hover{
  color: #fff;
}

section{
  display: flex;
  justify-content: center;
  width: 100%;
}

.bal-sect{
  margin: 10px;
  padding: 10px;
  background-color: #f4f4f4;
  color: rgb(17, 14, 173);
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 200px;
  width: 100%;
  border-radius: 15px;
}

.bal-sect-2{
  margin: 10px;
  padding: 10px;
  background-image: url(images/metaverse6.jpeg);
  background-size: cover;
  color: rgb(17, 14, 173);
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 430px;
  width: 100%;
  border-radius: 15px;
}

.bal-sect-3{
  padding: 10px;
  background-color: #f4f4f4;
  display: flex;
  max-width: 400px;
  width: 100%;
  border-radius: 15px;
}


header{
  max-width: 460px;
  width: 100%;
  padding: 10px;
  background-color: rgb(51, 143, 39);
  display: flex;
  justify-content: center;
  border-radius: 0 0 20px 20px;
  color: rgb(17, 14, 173);
}

.menu-sect{
  background-color: #ffffff;
  color: rgb(17, 14, 173);
  border-radius: 1rem;
  padding: 0.25rem 1rem;
  box-shadow: rgba(0, 0, 0, 0.2);
  font-weight: 600;
  margin-top: 0.5rem;
  max-width: 500px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}